export default{
  data() {
    return {
      articleList: '',
      theme: ''
    }
  },
  onLoad(opt) {
    console.log(opt.id)
    this.getDetail(opt.id)
  },
  methods: {
    getDetail(id) {
      let that= this,
        params= {
          url: that.$api.themeByArticleList,
          method: 'POST',
          data: {
            category_id: id
          }
        }
      that.$httpRequest(params).then(res => {
        that.theme= res.data.theme
      })
    }
  }
}
